/* CUBE ANIMATION */

.ani-container {
    position: relative;
    justify-content: center;
    align-items: center;
    height:-webkit-max-content;
    height:max-content;
    width: -webkit-max-content;
    width: max-content;
    margin: 0;
}
.ani-flex {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;

    /* Margin here is the vertical margin_you_want between the 3 layers */
    margin: 10px;
}

/* cube */
.cube {
    position: relative;
    width: 1px;
    height: 1px;
    /* Margin will only be horizontal, because there are 3 different layers containing 3 cubes each.
    
    Margin = (width_of_cube/2) + (margin_you_want/2)
    */
    margin: 45px;

    /* margin: 0 80px 0 0; */
    /* border: solid 1px rgb(70, 70, 70); */
    /* border-radius: 20px; */
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.cube:hover {
    background-color:#2e2e2e;
}

.wall {
    width: 80px;
    height: 80px;
    margin: 10px;
    position: absolute;
    left: calc(-80px / 2);
    top: calc(-80px / 2);
    text-align: center;
    line-height: 100px;
    border: solid 1px rgba(70, 70, 70);
    border-radius: 30px;
    
}
.front {
    -webkit-transform: translateZ(calc(80px / 2));
            transform: translateZ(calc(80px / 2));
}
.back {
    -webkit-transform: translateZ(calc(-80px / 2)) rotateY(180deg);
            transform: translateZ(calc(-80px / 2)) rotateY(180deg);
}
.right {
    -webkit-transform: translateX(calc(80px / 2)) rotateY(90deg);
            transform: translateX(calc(80px / 2)) rotateY(90deg);
}
.left {
    -webkit-transform: translateX(calc(-80px / 2)) rotateY(-90deg);
            transform: translateX(calc(-80px / 2)) rotateY(-90deg);
}
.top {
    /* transform: translateY(calc(-80px / 2)) rotateX(90deg); */
}
.bottom {
    /* transform: translateY(calc(80px / 2)) rotateX(-90deg); */
}
/* animation */
.ani-flex:nth-of-type(4) .cube:nth-of-type(1) {
    -webkit-animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.5s forwards;
            animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.5s forwards;
}
.ani-flex:nth-of-type(3) .cube:nth-of-type(1) {
    -webkit-animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.6s forwards;
            animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.6s forwards;
}
.ani-flex:nth-of-type(4) .cube:nth-of-type(2) {
    -webkit-animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.6s forwards;
            animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.6s forwards;
}
.ani-flex:nth-of-type(2) .cube:nth-of-type(1) {
    -webkit-animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.7s forwards;
            animation: rotation 3s cubic-bezier(0.215, 0.61, 0.355, 1) 0.7s forwards;
}
.ani-flex:nth-of-type(3) .cube:nth-of-type(2) {
