  }
  .cursor {
    font-weight: 700;
    -webkit-animation: 1s blink step-end infinite;
            animation: 1s blink step-end infinite;
  }
  @-webkit-keyframes blink {
    from,
    to {
      color: transparent;
    }
  
    50% {
      color: #00aa00;
      color: var(--text-color);
    }
  }
  @keyframes blink {
    from,
    to {
      color: transparent;
    }
  
    50% {
      color: #00aa00;
      color: var(--text-color);
    }
  }

